import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'
import Waypoint from 'react-waypoint'
import * as PropTypes from 'prop-types'

import Header from '../components/Header'
import Nav from '../components/Nav'
import pic01 from '../assets/images/pic01.jpg'

const propTypes = {
  data: PropTypes.object.isRequired,
}

class assignmentsTemplate extends React.Component {
  _handleWaypointEnter = () => {
    this.setState(() => ({ stickyNav: false }))
  }

  _handleWaypointLeave = () => {
    this.setState(() => ({ stickyNav: true }))
  }
  render() {
    const allcampagin = this.props.data.allcampagin.edges
    const assignments = this.props.data.assignments.edges
    //console.log("allcampagin"+allcampagin);
    const pathid = this.props.location.pathname
    var str_array = pathid.split('/')
    var p_id
    for (var i = 0; i < str_array.length; i++) {
      str_array[i] = str_array[i].replace(/^\s*/, '').replace(/\s*$/, '')
      p_id = str_array[i]
    }
    //console.log(p_id)
    //console.log(this.props.match.params.id);
    var arr1 = new Array()
    var identities
    var camName
    allcampagin.forEach(({ node }) => {
      node.campaigns.forEach(({ id, name, contents }) => {
        if (id == p_id) {
          /* console.log(name) */

          camName = name
          contents.forEach(({ id, name, identity }) => {
            //console.log(id);
            // console.log(name);
            //console.log(identity);
            identities = Buffer.from('assignment:' + identity).toString(
              'base64'
            )
            //console.log(identities);
            arr1.push(identities)
          })
        }
      })
    })

    var arr2 = new Array()
    var arr_title = new Array()
    assignments.forEach(({ node }) => {
      var assid
      var i
      for (i = 0; i <= arr1.length; i++) {
        assid = node.id
        if (arr1[i] == assid) {
          arr2.push({ id: node.id, title: node.title })
          // console.log(node.id);
          // console.log(node.title);
        }
      }
    })
    /* console.log('aa=>' + JSON.stringify(arr2)) */
    return (
      <div>
        <Helmet title={get(this, 'props.data.site.siteMetadata.title')} />
        <Header />
        <Waypoint
          onEnter={this._handleWaypointEnter}
          onLeave={this._handleWaypointLeave}
        />
        {/* <Nav sticky={this.state.stickyNav} /> */}
        <div id="main">
          <section id="first" className="main special">
            <h2 style={{ textAlign: 'left' }}>{camName}</h2>
            <nav>
              <ul
                style={{ listStyle: `disc`, width: `100%`, textAlign: 'left' }}
              >
                {arr2.map(({ id, title }, i) => (
                  <li>
                    <Link
                      to={`/assignmentdetails/` + id}
                      style={{ textDecoration: `none`, cursor: 'pointer' }}
                    >
                      {' '}
                      {title}
                    </Link>
                  </li>
                ))}
              </ul>
            </nav>
          </section>
        </div>
      </div>
    )
  }
}

assignmentsTemplate.propTypes = propTypes

export default assignmentsTemplate

export const CampaignPageQuery = graphql`
  query getAllCampaigns {
    allcampagin: allCampaignNameSpace {
      edges {
        node {
          id
          namespace
          campaigns {
            id
            name
            contents {
              id
              name
              identity
            }
          }
        }
      }
    }
    assignments: allAssignment {
      edges {
        node {
          id
          title
          postId
        }
      }
    }
  }
`



